.DashboardContainer {
  display: flex;
  flex-direction: column;

  .topbar {
    margin: 0;
    background-color: #2b3643;
    height: 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 40px;

    .dashlogo {
      // flex: 1;
      img {
        height: 3rem;
        width: auto;
      }
    }

    .user-logout {
      // flex: 0.2;
      color: #c6cfda;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      position: relative;
      height: 4rem;
      width: 11.25rem;
      cursor: default;

      &:hover {
        background-color: #3d4957;
        .drop-down {
          visibility: visible;
        }
      }

      .log-icon {
        height: 2.5rem;
        padding: 5px;
        border-radius: 50%;
        background-color: #f0f0f0;
      }

      .drop-icon {
        height: 1rem;
      }

      .drop-down {
        visibility: hidden;
        position: absolute;
        background-color: #333;
        padding: 0px 5px;
        top: 64px;
        width: 100%;
        text-align: center;
        cursor: pointer;
        letter-spacing: 2px;

        &:hover {
          color: whitesmoke;
        }
      }
    }
  }

  hr {
    border-color: #1f262f;
    padding: 0;
    margin: 0;
  }

  .nav-form {
    display: flex;
    nav {
      width: 12rem;
      float: left;
      background-color: #2b3643;
      min-height: 91vh;

      .sidebar {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        height: 20%;
        margin: 0;
        padding: 0;
        li {
          &:hover {
            background-color: #1f262f;
          }
          span {
            cursor: pointer;
            color: #c6cfda;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding: 1rem 2.3rem;

            img {
              height: 2rem;
              margin-right: 5px;
            }
          }

          hr {
            border-color: #2f3844;
            padding: 0;
            margin: 0;
          }
        }
      }
    }
  }
}
