.logincontainer {
  display: flex;
  flex-direction: row;
  width: 100vw;

  .bgimage {
    width: 100%;
    height: 100vh;
    overflow: hidden;

    img {
      height: auto;
      width: 50vw;
    }
  }
  .formcontainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;

    ul {
      display: flex;
      justify-content: center;

      li img {
        height: 5rem;
        width: 250px;
        //   background: center/contain url("../assets/SKSC-Logo.svg") no-repeat;
      }
    }

    .text {
      text-align: center;

      h1 {
        font-size: 24px;
      }

      p {
        font-size: 18px;
        color: #6a6c6f;
        margin: 0;
        margin-bottom: 0px;
        margin-bottom: 10px;
      }
    }

    form {
      width: 90%;
      label {
        width: 40vw;
        margin: 15px auto;
        position: relative;
        display: flex;
        justify-content: center;

        .placeholder {
          position: absolute;
          top: -13px;
          left: 18px;
          transition-property: top;
          transition-duration: 0.1s;
          background: #fff;
          font-size: 14px;
          font-weight: 500;
          z-index: 1;
          cursor: initial;
          padding: 0px 3px;
          border-radius: 2px;

          .required {
            z-index: 1;
            color: #e02222;
            font-size: 18px;
            padding-left: 2px;
            height: 0;
            width: 0;
          }
        }

        input {
          width: 40vw;
          height: 40px;
          padding: 15px;
          font-size: 18px;
          color: #000000;
          margin-bottom: 0;
          border: solid 1px rgba(0, 0, 0, 0.2);
          outline: none;

          &:hover {
            border-color: #58595b;
          }
        }
      }

      .submit-btn {
        margin: 0 auto;
        display: flex;
        justify-content: center;

        input {
          font-size: 18px;
          font-weight: 400;
          letter-spacing: 2px;
          width: 40vw;
          padding: 8px 0px;
          display: block;
          background: #58595b;
          color: #fff;
          margin-top: 30px;
          border: none;
          z-index: 1;
          position: relative;

          &:hover {
            background-color: #b7202e;
            cursor: pointer;
          }
        }
      }
    }
  }
}
